<template>
  <div>
           <v-btn
              fixed
              dark
              fab
              bottom
              right
              color="indigo"
            @click="this.onSave"
            >
              <v-icon>mdi-download</v-icon>
            </v-btn>
<v-layout justify-center wrap>
    <v-flex xs12 md6 pa-5> 
  <v-file-input type="file"  accept="image/png, image/jpeg, image/bmp"
    placeholder="Upload a Photo"
    prepend-icon="mdi-upload"
    label="Upload Photo" @change="this.onImageChange" :clearable="false" v-model="images" show-size></v-file-input>
    <center>
        or
    </center>
    <v-file-input class="hidden-md-and-up" type="file"  accept="image/*"
    placeholder="Pick a Photo"
    prepend-icon="mdi-camera"
    label="Take Photo" capture="" @change="this.onImageChange" :clearable="false" v-model="images" show-size></v-file-input>
    </v-flex>
    <v-flex xs12 md6 pa-5>
    <div id="my-node-">
   <v-img :src="imageUrl" width="100%" height="auto" alt="Alternative" class="firstImage">
       <v-img :src="rlogo" alt="" width="100%" class="secondImage" />
   </v-img>
</div>
    </v-flex>
</v-layout>
<div style="opacity:0" >
  <div id="my-node">
   <v-img :src="imageUrl" width="100%" height="auto" alt="Alternative" class="firstImage">
       <v-img :src="rlogo" alt="" width="100%" class="secondImage" />
   </v-img>
</div>
</div>
  
  </div>
</template>

<script>
import domtoimage from 'dom-to-image';
import rlogo from "@/assets/rlogo.png"

export default {
  name: 'Camera',
  data () {
    return {
      imageUrl: null,
      rlogo: rlogo,
      images: null,
    }
    },
    updated(){
      
    },
    methods: {
      onSave(){
        var FileSaver = require('file-saver');
        var node = document.getElementById('my-node');
        var today = new Date();
        var name = today.getFullYear()+'-'+(today.getMonth()+1)+'-'+today.getDate() + today.getHours() + ":" + today.getMinutes() + ":" + today.getSeconds();
    
    //        domtoimage.toPng(node)
    // .then(dataUrl => {
    //   const link = document.createElement('a');
    //   link.download = 'image.png';
    //   link.href = dataUrl;
    //   link.click();
    // })
          

          domtoimage.toBlob(node)
    .then(function (blob) {
        FileSaver.saveAs(blob, name + 'Rotaract-Club-of-Majestic-CBE.png');
    }).catch(function (error) {
        console.error('oops, something went wrong!', error);
    });
      },
      onImageChange(){
          this.imageUrl =URL.createObjectURL(this.images)
      }
    },
};
</script>


<style scoped>

#my-node {
    background: white;
    position: relative;
}

.secondImage{
    height: inherit;
    position: absolute;
    bottom: 0;
    z-index: 99;
}
</style>